body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.play-button {
  height: 40px;
  left: calc(50vw - 20px);
  margin: auto;
  position: absolute;
  top: calc(50vw*(320/568) - 20px);
}

.play-button:hover {
  -webkit-filter: invert(.15);
          filter: invert(.15);
  cursor: pointer;
}
.company-title {
  align-items: center;
  color: #fff;
  display: flex;
  font-family: 'Noto Serif', serif;
  font-size: 36px;
  margin: 0px;
}

.company-title:hover {
  color: #ddd;
  cursor: pointer;
}

.hamburger-ico {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.hide {
  display: none;
}

.nav-container {
  display: flex;
  flex-direction: column;
  height: 495px;
  justify-content: flex-end;
  left: 30px;
  position: absolute;
  width: 365px;
}

.nav-link {
  font-family: 'Noto Serif', serif;
  font-size: 32px;
  margin: 20px 0;
}

.nav-link:hover {
  color:#ddd;
  cursor: pointer;
}

.nav-links {
  color: #fff;
  list-style: none;
  margin: 0;
}

.nav-title {
  display: flex;
}

.nav-title-bottom {
  -webkit-transform: translateY(calc((100vw * 6 / 16)) );
          transform: translateY(calc((100vw * 6 / 16)) );
}
.overlay {
  position: absolute;
  width: 100vw;
  height: calc(100vw * 9 / 16);
  top: 0;
}

.player-wrapper {
  /* background-color: #111; */
  /* filter: opacity(.5); */
  /* filter: invert(.5); */
  position: relative;
  padding-top: calc(100%/(568/320)) /* Player ratio: 100 / (1280 / 720) */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}
h2 {
  color: #000000;
  font-size: 50px;
  font-family: "Suisse", sans-serif;
  font-weight: 300;
  line-height: 60px;
  letter-spacing: .02em;
  padding-left: 50px;
}
.app-grid-container {
  display: grid;
  grid-template-columns: 1fr;
}
